@import './../../global';

.HeaderHolder{
    background-color: $background;
    position: sticky;
    top: 0;
    height: $header-height;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid RGB(0, 0, 0, 0.5);
    z-index: 100;
    height: fit-content;
    .HeaderLogoHolder{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
    }
    .HeaderLogo{
        fill: $white;
        height: 50px;
        width: 0px;
        align-self: center;
        margin-top: 1rem;
        animation: closeImg 0.45s ease-in 0s;
        @keyframes closeImg {
            0%{
                width: 350px;
            }
            100%{
                width: 0px;
            }
        }
        
    }
    @media only screen and (max-width: 600px) {
        
        .HeaderLogo{
            @keyframes closeImg {
                0%{
                    width: 350px;
                }
                100%{
                    width: 0px;
                }
            }
        }
    }
    
    .HeaderLogoAlt{
        color: $white;
        fill: $white;
        font-weight: bold;
        font-size: 3.5rem;
        align-self: center;
        margin-top: 1rem;
        margin-bottom: 0rem;
        
    }
    .HeaderLogoAltBy{
        color: $white;
        fill: $white;
        font-weight: bold;
        font-size: 2rem;
        align-self: center;
        margin-top: 1rem;
        margin-bottom: 0rem;
        margin-left: 0rem;
        margin-right: 0rem;
        width: 0px;
        overflow: hidden;
        animation: close 0.5s ease-out 0s;
        @keyframes close {
            0%{
                width: 2rem;
                margin-left: 1rem;
                margin-right: 1rem;
            }
            90%{
                width: 2rem;
                margin-left: 1rem;
                margin-right: 1rem;
            }
            95%{
                margin-right: 0.5rem;
            }
            100%{
                width: 0px;
                margin-left: 0rem;
        margin-right: 0rem;
            }
        }
    }
    .SidebarButton{
        position: absolute;
        right: 0;
        margin-top: auto;
        color: $white;
        background: none;
        border: none;
        cursor: pointer;
        //align vertical
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 5rem;
        scale: 1.2;
        @media only screen and (max-width: 768px) {
            //disable
            position: relative;
            width: 100%;
            height: 5rem;
        }

    }
}

$sidebar-width:18%;

.SidebarOutside{
    position:fixed;
    width: 100%;
    height: 100vh;
    background-color: $white;
    opacity: 0.5;
    z-index: 998;
    right: 0px;
    left: 0px;
    top: 0px;
    animation: fadeIn 0.4s ease-in-out;
    @keyframes fadeIn {
        0%{
            opacity: 0;
        }
        100%{
            opacity: 0.5;
        }
        
    }
}

.Sidebar {
    position: fixed;
    top: 0;
    right: -100%;
    width: $sidebar-width;
    height: 100vh;
    background-color: $black;
    transition: right 0.4s ease-in-out;
    z-index: 999;
    @media only screen and (max-width: 768px) {
        width: 100%;
    }
}
  
.SidebarOpen {
    right: 0;
}
  
.MenuList{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20%;
    .MenuItem{
        color: $white;
        font-size: 1.3rem;
        margin-bottom: 0.5rem;
        text-decoration: none; // added property
        user-select: none;
        &:hover{
            color: $olive;
            text-decoration: solid underline $olive;
            cursor: pointer;
            
        }
    }
    .Back{
        background-color: $white;
        align-items: center;
        justify-content: center;
        width: 5rem;
        height: 5rem;
    }
}
.PopupFrom{
    color: $white;
    width: 100%;
    height: 100%;

    @media only screen and (max-width: 768px) {
        width: 100%;
    }
}
.PopupFormTitle{
    color: $white;
    pointer-events: none;
    //no select
    user-select: none;
    // underline
    text-decoration: underline;
}
.PopupFormInputHolder{
    color: $white;
}
.PopupFormButton{
    background-color: $white;
    color: $black;
    border: 1px solid $white;
    border-radius: 1px;
    padding: 0.5rem;
    margin: 0.5rem;
    cursor: pointer;
    &:hover{
        background-color: $olive;
        color: $black;
        border: 1px solid $black;
    }
}


.PopupFormSubtitle{
    color: $white;
    //no click
    pointer-events: none;
    //no select
    user-select: none;

}