@import "./../../global";

.PopupBack{
    z-index: 1499;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: $black;
    opacity: 0.5;
}

.PopupHolder{
    position: fixed;
    background-color: $grey;
    min-width: fit-content;
    height: fit-content;
    min-height: fit-content;
    z-index: 1500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    border-radius: 5px;
    padding: 1rem;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 1rem;
}